.App {
  text-align: center;
}
.background {
  background-image:url('./031c216056fcca3ecf273249b336f4d9.jpg');
  background-size: cover;
  height: 300px;
  color: white;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
